import { ToastPosition } from "@chakra-ui/react";

const CustomErrorToast = (
  toast: any,
  msg: string,
  description: string,
  duration?: number,
  pos?: ToastPosition | undefined
) => {
  toast({
    title: msg,
    description,
    status: "error",
    duration: 2000 || duration,
    isClosable: true,
    position: pos ? pos : "top-right",
  });
};

export default CustomErrorToast;
