// 
import {
  Flex,
  Text,
  Spacer,
  Image,
  Stack,
  Input,
  InputGroup,
  InputLeftElement,
  Circle,
  Box,
  Divider,
  Icon,
  Spinner,
  Checkbox,
  Skeleton,
  Button,
  useToast,
  Heading,
  VStack,
  useDisclosure,
  Avatar,
  useSteps,
  Step,
  StepDescription,
  StepIcon,
  StepIndicator,
  StepNumber,
  StepSeparator,
  StepStatus,
  StepTitle,
  Stepper,
  Progress,
} from "@chakra-ui/react";
import React, { lazy, Suspense, useEffect, useState } from "react";
import { FaSearch } from "react-icons/fa";
import Images_Icons from "../../constants/icons-images";
import Container from "../../components/Container";
import { addToSwap, clearSwap, setCostPayerId } from "../../redux/TokenSwapSlice";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";
import { RootState } from "../../redux/store";
import { useLocation, useNavigate } from "react-router-dom";
import { nanoid } from "@reduxjs/toolkit";
import { getTokenBasedRewards } from "../../redux/ProductsSlice";
import { AuthReducerProps } from "../../interfaces";
import { useGetWallet } from "../../hooks/tokenBalance";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import { ethers } from "ethers";
import Lottie from "lottie-react";
import MeProtocolButton from "../../components/MeProtocolButton";
import ModalComponent from "../../components/ModalComponent";
import checkMark from "../../assets/json/checkMark.json";
import spinning_loader from "../../assets/json/spinning_loader.json";
import { HOW_IT_WORKS, BOUNTIES_TEXT_DEMO } from "../../constants/FAQJSON";
import { GLOBAL_PREFIX } from "../../constants";
import { useMeProtocol } from "me-protocol";
import axios from "axios";
import { BASE_URL, ME_API_KEY } from "../../config";
import CustomErrorToast from "../../components/CustomErrorToast";

const RedeemNavBar = lazy(() => import("../../components/RedeemNavbar/index"));

export const RedeemSubsequent = (): JSX.Element => {
  const dispatch = useDispatch<Dispatch>();
  const {
    getExpectedAmountOfTargetedReward,
    spendRewardsOnAnotherBrandWithVaultPermit,
    spendRewardsOnIssuingBrandWithVaultPermit,
    spendingSteps,
  } = useMeProtocol();

  const { state }: any = useLocation();
  const toast = useToast();
  let navigate = useNavigate();

  const steps = [
    { title: "First", description: "Validating" },
    { title: "Second", description: "Performing Redemption" },
    { title: "Third", description: "Completing Redemption" },
  ];

  const { activeStep, setActiveStep } = useSteps({
    index: 0,
    count: steps.length,
  });

  // const { isConnected } = useAccount();

  const [searchOpen, setSearchOpen] = useState<boolean>(false);
  const [tokenChecked, setTokenChecked] = useState<boolean>(false);
  const [searchValue, setSearchValue] = useState<string>("");
  const [orderId, setOrderId] = useState<string>("");
  const [activeCheckIndex, setActiveCheckIndex] = useState<number | null>(null);

  const [tokenBalance, setTokenBalance] = useState<any>({});
  const [ticker, setTicker] = useState<string>("");

  const [redeemLoading, setRedeemLoading] = useState<boolean>(false);
  const [neededValueLoading, setNeededValueLoading] = useState<boolean>(false);
  const [neededTokens, setNeededTokens] = useState<string>("");
  const [neededTokenInBigNumber, setNeededTokensInBigNumber] = useState<any>();
  const [rerunTokenFetch, setRerunTokenFetch] = useState<boolean>(false);

  const {
    isOpen: syncSuccessIsOpen,
    onOpen: syncSuccessOnOpen,
    onClose: syncSuccessOnClose,
  } = useDisclosure();
  const {
    isOpen: progressModalIsOpen,
    onOpen: progressModalOnOpen,
    onClose: progressModalOnClose,
  } = useDisclosure();

  const {
    root,
    root2,
    nameStyles,
    boxCircleColor,
    leftSection,
    rightSection,
    font,
    font1,
    subHeadStyle,
    headingStyle,
    circleReward,
    searchFlex,
    balanceFlex,
    balanceButton,
    inputStack,
    imageBrandLogo,
    doneBox,
    rightSectionImage,
    dealPriceFlex,
    differenceFlex,
    subtotalFlex,
    headingsFlex,
    editText,
    boxPrice,
    boxPercent,
  } = useRedeemStyles();

  // useEffect(() => {
  //   console.log(spendingSteps, "spendingSteps");
  // }, [spendingSteps]);

  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);

  const { swapTokens }: any = useSelector((state: RootState) => state.tokenSwapReducer);

  const { reward_type_product, reward_type_loading } = useSelector(
    (state: RootState) => state.productReducer
  );

  const { getTokenBalance, loading: tokenBalanceLoading } = useGetWallet();
  // const contractAddress = "0x970383043fd7f021fd5000ac173ac6e7be5eb296";
  const contractAddress = swapTokens[0]?.contractAddress;

  // console.log(swapTokens[0]);

  useEffect(() => {
    (async () => {
      setTokenBalance(await getTokenBalance(contractAddress));
    })();
  }, [swapTokens, rerunTokenFetch]);

  useEffect(() => {
    CALC_NEEDED_REWARDS();
    dispatch(getTokenBasedRewards({ token }));
    // SET COST PAYER ID
    dispatch(setCostPayerId(state?.brandId));
  }, []);

  // console.log(reward_type_product);
  // console.log(reward_type_product.find((item: any) => item?.brandId === state?.brandId));

  useEffect(() => {
    setActiveStep(spendingSteps);
    if (spendingSteps > 0) progressModalOnOpen();
  }, [spendingSteps]);
  useEffect(() => {
    if (reward_type_product?.length > 0) {
      dispatch(clearSwap());
      dispatch(
        addToSwap(reward_type_product.find((item: any) => item?.brandId === state?.brandId))
      );
    }
    setFilteredSearch(reward_type_product ? [...reward_type_product].reverse() : []);
    setMasterData(reward_type_product ? [...reward_type_product].reverse() : []);
  }, [reward_type_product]);

  const [filteredSearch, setFilteredSearch] = useState([]);
  const [masterData, setMasterData] = useState([]);

  // SEARCH FUNCTION
  function SearchFilter(text: string) {
    if (text) {
      const newData = masterData.filter((item: any) => {
        const itemData = item?.brand?.brandName
          ? item?.brand?.brandName.toUpperCase()
          : "".toUpperCase();
        const textData = text.toUpperCase();
        return itemData.indexOf(textData) > -1;
      });
      setFilteredSearch(newData);
      setSearchValue(text);
    } else {
      setFilteredSearch(masterData);
      setSearchValue(text);
    }
  }

  console.log(state?.tokens?.toString());

  // console.log(state?.reward?.contractAddress, contractAddress, "ESATÃ¥");
  // console.log(spendingSteps, "MY STEPS");

  //JUNE 2023 new fn for swap =================================================================

  const CALC_NEEDED_REWARDS = async () => {
    if (!state?.reward?.contractAddress || !contractAddress) return;
    setNeededValueLoading(true);

    try {
      if (swapTokens[0]?.brandId === state?.brandId) {
        setNeededTokens(state?.tokens.toString());
        return;
      }
      // console.log(state?.reward?.contractAddress, contractAddress, "COMPare");

      let expectedAmount: any = await getExpectedAmountOfTargetedReward({
        amount: state?.tokens.toString(),
        inputRewardAddress: contractAddress,
        outPutRewardAddress: state?.reward?.contractAddress,
      });

      // expectedAmount = expectedAmount + ethers.utils.parseEther("1");

      setNeededTokens(Number(ethers.utils.formatUnits(expectedAmount, 18)).toFixed(2));
      setNeededTokensInBigNumber(expectedAmount);

      // console.log(expectedAmount);
    } catch (error) {
      console.log(error, "CALC_NEEDED_REWARDS_ER");
    } finally {
      setNeededValueLoading(true);
    }
  };

  const SWAP_FUNCTIONALITY = async () => {
    try {
      if (swapTokens[0]?.brandId === state?.brandId) {
        return;
      }
      setRedeemLoading(true);

      // console.log(state?.reward?.contractAddress, contractAddress, "@ ADDS");

      // console.log(state?.reward?.id);

      const { data }: any = await axios.post(
        `${BASE_URL}/order/redeem-offer`,
        {
          offerId: state.id,
          quantity: 1,
        },
        { headers: { Authorization: `Bearer ${token}` } }
      );

      if (data?.data?.id) {
        const swapRes: any = await spendRewardsOnAnotherBrandWithVaultPermit({
          rewardId: state?.reward?.id,
          spendInfo: {
            rewardAtHand: contractAddress,
            targettedReward: state?.reward?.contractAddress,
            amountOfRewardAtHand: neededTokenInBigNumber,
            expectedAmountOfTargetedReward: ethers.utils.parseEther(state?.tokens.toString()),
          },
        });

        if (swapRes?.taskId) {
          const { data: completeData }: any = await axios.post(
            `${BASE_URL}/order/complete`,
            {
              orderId: data?.data?.id,
              taskId: swapRes?.taskId,
            },
            { headers: { Authorization: `Bearer ${ME_API_KEY}` } }
          );
          syncSuccessOnOpen();
          setRedeemLoading(false);
          setOrderId(data?.data?.id);

          // navigate(`${GLOBAL_PREFIX}/dashboard_my_deals_details`, {
          //   state: { orderId: data?.data?.id },
          // });
        }
      }
    } catch (error: any) {
      console.log(error);

      // console.log(error?.response?.data?.message, "SWAP_FUNCTIONALITY");
      CustomErrorToast(toast, "Error", error?.response?.data?.message, 2000);
    } finally {
      setRedeemLoading(false);
      progressModalOnClose();
    }
  };

  const SWAP_FOR_SAME_BRAND = async () => {
    setRedeemLoading(true);

    try {
      const { data }: any = await axios.post(
        `${BASE_URL}/order/redeem-offer`,
        {
          offerId: state.id,
          quantity: 1,
        },
        { headers: { Authorization: `Bearer ${token}` } }
      );

      if (data?.data?.id) {
        const swapRes = await spendRewardsOnIssuingBrandWithVaultPermit({
          reward_address: state?.reward?.contractAddress,
          reward_amount: ethers.utils.parseEther(state?.tokens.toString()),
          rewardId: state?.reward?.id,
        });

        if (swapRes?.taskId) {
          const { data: completeData }: any = await axios.post(
            `${BASE_URL}/order/complete`,
            {
              orderId: data?.data?.id,
              taskId: swapRes?.taskId,
            },
            { headers: { Authorization: `Bearer ${ME_API_KEY}` } }
          );
          syncSuccessOnOpen();
          setRedeemLoading(false);
          setOrderId(data?.data?.id);

          // navigate(`${GLOBAL_PREFIX}/dashboard_my_deals_details`, {
          //   state: { orderId: data?.data?.id },
          // });
        }
      }
    } catch (error: any) {
      console.log(error, "SWAP_FOR_SAME_BRANDERR");
      CustomErrorToast(toast, "Error", error?.response?.data?.message, 2000);

      // onCloseSteps();
    } finally {
      setRedeemLoading(false);
      progressModalOnClose();
    }
  };

  const activeStepText = steps[activeStep - 1]?.description;

  const max = steps.length - 1;
  const progressPercent = (activeStep / max) * 100;

  return (
    <Box w="100%" h="100vh">
      <Suspense fallback={<Spinner size="sm" />}>
        {/* ---------------------------------------------------------------------- REDEEM NAV BAR------------------------------------------------------------ */}
        <RedeemNavBar />
        {/* ---------------------------------------------------------------------- THE END REDEEM NAV BAR------------------------------------------------------------ */}
      </Suspense>

      <Flex flexDir={"column"}>
        <Container>
          <Stack {...root}>
            <Flex mb={"25px"}>
              {/* ---------------------------------------- CONGRATS You --------------------------------------------*/}
              {/* <Text {...nameStyles}>Pay in rewards</Text> */}
              <Spacer />
              {/* ----------------------------------------CONNECT WALLET --------------------------------------------*/}
            </Flex>

            <Flex>
              {/* -----------------------------------LEFT SECTION---------------------------------------------- */}
              <Flex {...leftSection} flexDir={"column"} pos="relative">
                {/* ----------------------------------------------------------------LEFT TOP DETAIL ----------------------------------------------------------------*/}

                {/* ---------------------------------------------------------------- TOP CONTAINER ----------------------------------------------------------------*/}
                <Flex
                  {...root2}
                  flexDir={"row"}
                  justifyContent={"space-between"}
                  alignItems={"center"}
                >
                  <Flex>
                    {/* ---------------------- BRAND LOGO REPRESENT THE AVATAR ----------------------------- */}
                    {swapTokens?.map((e: any) => (
                      <Flex flexDir={"row"} key={nanoid()}>
                        <Circle
                          key={nanoid()}
                          as={Image}
                          objectFit={"cover"}
                          mr={swapTokens.length > 1 ? "-15px" : "0px"}
                          {...circleReward}
                          src={e?.rewardImage || Images_Icons.appLogoPlaceholder}
                          fallbackSrc={Images_Icons.appLogoPlaceholder}
                          fallbackStrategy="beforeLoadOrError"
                        />
                        <Stack pl="10px" h="40px">
                          <Text color={"dark"}>{e?.brandName}</Text>
                          <Skeleton isLoaded={tokenBalanceLoading}>
                            <Text color={"#999999"}>
                              {Number(tokenBalance?.balance).toFixed(2) || 0} {e?.rewardSymbol}
                            </Text>
                          </Skeleton>
                        </Stack>
                      </Flex>
                    ))}
                  </Flex>

                  {/* ---------------------------- ADD OR REMOVE REWARDS ---------------------- */}
                  <Flex {...searchFlex} as="button" onClick={() => setSearchOpen(true)}>
                    <Text {...headingStyle}>Redeem with another reward</Text>
                    {/* <Text {...headingStyle}>Add/remove rewards</Text> */}
                    <Image objectFit={"contain"} src={Images_Icons.arrowLeftIcon} />
                  </Flex>
                </Flex>

                <Spacer />

                {/* ----------------------------- CONFIRM TRANSACTIONS BUTTON --------------------------------- */}

                <Flex {...balanceFlex} flexDir={"column"}>
                  <Button
                    {...balanceButton}
                    isLoading={redeemLoading}
                    disabled={Number(tokenBalance?.balance) < Number(neededTokens)}
                    onClick={() => {
                      if (swapTokens[0]?.brandId !== state?.brandId) {
                        // TRY_SWAP_DIFF_PERMIT();
                        SWAP_FUNCTIONALITY();
                        return;
                      } else {
                        // NEW_TRY_SINGLE_PERMIT();
                        if (Number(tokenBalance?.balance) < Number(state?.tokens)) {
                          MeProtocolToast(
                            toast,
                            "Not enough rewards for swap, please select another reward",
                            2000,
                            "top"
                          );
                          return;
                        }
                        SWAP_FOR_SAME_BRAND();
                      }
                    }}
                  >
                    Confirm transaction
                  </Button>

                  <Text mt={"24px"} {...font1}>
                    Ensure you check the terms and conditions for claiming this offer.
                  </Text>
                </Flex>

                {/* ----------------------------- THE END OF CONFIRM TRANSACTIONS BUTTON --------------------------------- */}

                {/* ----------------------------------------------------------------------SEARCH SECTION --------------------------------------------*/}
                {searchOpen && (
                  <Stack {...inputStack} position={"absolute"}>
                    <InputGroup _focus={{ boxShadow: "none" }} as="button">
                      <InputLeftElement
                        pointerEvents="none"
                        children={<Icon as={FaSearch} color="#959595" />}
                        p={"1rem"}
                      />
                      <Input
                        {...font}
                        value={searchValue}
                        onChange={({ target }: any) => {
                          setSearchValue(target.value);
                          SearchFilter(target?.value);
                        }}
                        placeholder="Search tokens..."
                        borderRadius="0"
                        borderWidth="0"
                        // _focus={{ boxShadow: "none" }}
                      />
                      <Flex w="150px">
                        <Text {...font}>Import tokens</Text>
                      </Flex>
                    </InputGroup>
                    <Divider borderColor={"#00000030"} w={"100%"} p={0} />

                    <>
                      {filteredSearch?.map((e: any, i: number) => (
                        <Box key={nanoid()}>
                          <Flex px={"3%"} py={"1%"} align={"center"}>
                            {/* ---------------------- BRAND LOGO REPRESENT THE AVATAR ----------------------------- */}
                            <Image src={e?.rewardImage} objectFit={"cover"} {...imageBrandLogo} />
                            {/* ---------------------- THE BRAND NAME AND TOKEN DETAILS ----------------------- */}
                            <Stack pl={"2%"}>
                              <Text fontSize={"16px"} fontWeight={"400"}>
                                {/* {e.name} */}
                                {e?.brandName}
                              </Text>
                              <Text fontSize={"14px"} color="#999999">
                                {/* {e.tokens} {e.symbol} */}
                                {e?.rewardSymbol}
                              </Text>
                            </Stack>
                            <Spacer />

                            <Flex pl={"2%"}>
                              {/* ------------------------- Check Mark Button ------------------- */}
                              <Checkbox
                                spacing={"0.5rem"}
                                // defaultChecked
                                colorScheme={"gray"}
                                isChecked={i === activeCheckIndex}
                                onChange={({ target }: { target: { checked: boolean } }) => {
                                  setActiveCheckIndex(i);
                                  setTokenChecked(target.checked);
                                  dispatch(clearSwap());
                                  dispatch(
                                    addToSwap({
                                      ...e,
                                      quantity: 1,
                                    })
                                  );
                                }}
                              />
                            </Flex>
                          </Flex>
                          <Divider borderColor={"#00000030"} w={"100%"} p={0} />
                        </Box>
                      ))}

                      {/* ----------------------------- DONE BUTTON SECTION --------------------------------- */}

                      <Flex
                        justify={"center"}
                        alignItems={"center"}
                        onClick={() => setSearchOpen(false)}
                      >
                        <Box as={"button"} {...doneBox} onClick={() => CALC_NEEDED_REWARDS()}>
                          {/* <Box as={"button"} {...doneBox} onClick={() => TRY_ETHERS()}> */}
                          Done
                        </Box>
                      </Flex>
                    </>
                  </Stack>
                )}
                {/* ---------------------------------------------------------------------- THE END OF SEARCH SECTION --------------------------------------------*/}

                <Spacer />

                <Divider orientation="horizontal" />

                <Flex flexDir={"column"} mt="24px">
                  <Text {...nameStyles}>How it works</Text>

                  {HOW_IT_WORKS?.map((e: { text: string }, i) => (
                    <Flex mt="20px" key={nanoid()}>
                      <Circle size="25px" {...boxCircleColor}>
                        <Text>{i + 1}</Text>
                      </Circle>
                      <Text {...font1} ml={"10px"} textAlign="justify">
                        {e.text}
                      </Text>
                    </Flex>
                  ))}
                </Flex>

                <Spacer mt="20px" />

                <Divider orientation="horizontal" />

                <Flex justify="space-between" mt="20px">
                  <Text fontSize="18px">Or earn with bounties</Text>
                  <Text
                    fontSize="16px"
                    textDecoration="underline"
                    onClick={() => navigate(`${GLOBAL_PREFIX}/bounties`)}
                  >
                    See all bounties
                  </Text>
                </Flex>
                {/* ---------------------------------------- BOUNTIES DETAILS -------------------------------------------- */}
                {BOUNTIES_TEXT_DEMO?.map(
                  (e: { title: string; body: string; titleTiming: string; time: string }) => (
                    <Flex justify="space-between" mt="15px" key={nanoid()}>
                      <Flex>
                        {/* -------------------- BRAND LOGO FOR THE BOUNTIES ----------------- */}
                        <Avatar src="" mr="8px" />
                        <Flex flexDir="column">
                          <Text {...font1}>{e.title}</Text>
                          <Text fontSize="18px">{e.body}</Text>
                        </Flex>
                      </Flex>

                      <Flex flexDir="column">
                        <Text {...font1}>{e.titleTiming}</Text>
                        <Text fontSize="16px" color="#C90303">
                          {e.time}
                        </Text>
                      </Flex>
                    </Flex>
                  )
                )}
              </Flex>

              <Spacer m={"1%"} />

              {/* -----------------------------------RIGHT SECTION---------------------------------------------- */}
              <Flex {...rightSection} flexDir={"column"}>
                <Flex>
                  <Image
                    src={state?.offerImages[0]?.url}
                    {...rightSectionImage}
                    objectFit={"contain"}
                    fallbackSrc={Images_Icons.appLogoPlaceholder}
                    fallbackStrategy="beforeLoadOrError"
                  />

                  <Spacer mr={"6px"} />

                  <Flex flexDir={"column"}>
                    <Text fontSize={"13px"} fontWeight="500">
                      {state?.name}
                    </Text>
                    <Spacer />
                    <Text fontSize={"14px"} color="#999999">
                      {state?.tokens} {state?.reward?.rewardSymbol}
                    </Text>
                    <Text fontSize={"14px"} color="#999999">
                      {state?.price} USD
                    </Text>
                    <Text fontSize={"14px"} color="#999999">
                      Quantity: {" 1 "}
                    </Text>
                  </Flex>
                </Flex>
                {/* ------------------------------ Deal Price ------------------ */}
                <Flex flexDir={"row"} {...dealPriceFlex}>
                  <Text {...subHeadStyle}>Deal Price</Text>
                  <Flex>
                    <Box as="button" color="black" px={2} h={6}>
                      {/* ${state?.price?.toFixed(2)} */}
                      {state?.tokens} {state?.reward?.rewardSymbol}
                    </Box>
                  </Flex>
                </Flex>
                {/* -------------------------------- Difference ---------------------------- */}
                <Flex flexDir={"row"} {...differenceFlex}>
                  <Text {...subHeadStyle}>Needed reward </Text>
                  <Flex>
                    {/* <Skeleton isLoaded={!neededValueLoading}> */}
                    <Text as="button" px={2} h={6}>
                      {neededTokens} {swapTokens[0]?.rewardSymbol}
                      {/* ${total.toFixed(2)} */}
                      {/* {tokenBalance?.balance || 0} {state?.reward?.rewardSymbol} */}
                    </Text>
                    {/* </Skeleton> */}
                  </Flex>
                </Flex>

                {/* -------------------------------- Subtotal reward ---------------------------- */}
                <Flex flexDir={"row"} {...subtotalFlex}>
                  <Text {...subHeadStyle}>Available rewards</Text>
                  <Flex>
                    <Box as="button" color="black" px={2} h={6}>
                      {/* $0.00 */}
                      {Number(tokenBalance?.balance).toFixed(2) || 0} {swapTokens[0]?.rewardSymbol}
                    </Box>
                  </Flex>
                </Flex>

                <Spacer />

                {Number(tokenBalance?.balance) < Number(neededTokens) && (
                  <Flex mt={"20px"}>
                    <Text fontSize={"14px"} color="#C90303">
                      You do not have enough reward to claim this offer for the selected reward,
                      please select a different reward to complete the reward.
                    </Text>
                  </Flex>
                )}

                {/* ------------------------------------------ PAYMENT DETAILS ------------------------------------- */}
                <Flex flexDir={"column"}>
                  <Text {...nameStyles} textAlign={["left"]} mt={"45px"}>
                    Payment Details
                  </Text>

                  {/* ------------------------------ headings ------------------ */}
                  <Flex flexDir={"row"} {...headingsFlex}>
                    <Text fontSize={"14px"} color="#AEAEAE">
                      Rewards slipt
                    </Text>
                    <Text
                      {...editText}
                      as="button"
                      onClick={() => {
                        setSearchOpen(true);
                      }}
                    >
                      Edit
                    </Text>
                  </Flex>

                  {/*  ----------------------------- BRAND ONE DETAILS ---------------------------- */}
                  {swapTokens?.map((e: any, i: any) => (
                    <Box key={nanoid()}>
                      <Flex
                        flexDir={"column"}
                        padding="6px"
                        bg={i % 2 === 0 ? "#F2F2F2" : "#FFFFFF"}
                      >
                        <Flex flexDir={"row"} justify={"space-between"}>
                          <Text {...subHeadStyle}>{e?.brandName}</Text>
                          <Flex>
                            <Box as="button" {...boxPrice}>
                              {state.price} USD
                            </Box>
                          </Flex>
                        </Flex>

                        <Flex flexDir={"row"} justify={"space-between"} color="#999999">
                          <Text {...subHeadStyle}>
                            {neededTokens} {swapTokens[0]?.rewardSymbol}
                            {/* {e.tokens} {e.symbol} */}
                          </Text>
                          <Flex>
                            <Box as="button" {...boxPercent}>
                              100 %{/* {e.percentageChange} % */}
                            </Box>
                          </Flex>
                        </Flex>
                      </Flex>
                    </Box>
                  ))}
                </Flex>
              </Flex>

              {/* ----------------------------------- THE END OF RIGHT SECTION---------------------------------------------- */}
            </Flex>
          </Stack>
          {/* ----------------------------------------SUCCESS MODAL-------------------------------------- */}
          <Suspense fallback={<Spinner size="sm" />}>
            <ModalComponent
              isOpen={syncSuccessIsOpen}
              onClose={syncSuccessOnClose}
              closeOnOverlayClick={true}
            >
              <Stack w="100%">
                <VStack>
                  <Lottie
                    style={{ width: "150px", height: "150px" }}
                    animationData={checkMark}
                    loop={false}
                    autoplay
                  />
                  <Heading fontSize={"18px"} pb="10px">
                    Reward redeemed successfully
                  </Heading>
                  <Suspense fallback={<Spinner size="sm" />}>
                    <MeProtocolButton
                      name="Ok"
                      onClick={async () => {
                        window.scrollTo({ top: 0, behavior: "smooth" });
                        // GET_ALL_SYNCED_REWARDS();
                        // TRY_ETHERS();
                        await CALC_NEEDED_REWARDS();
                        setTokenBalance(await getTokenBalance(contractAddress));
                        setRerunTokenFetch((prev) => !prev);
                        setRedeemLoading(false);
                        // navigate(`${GLOBAL_PREFIX}/dashboard/dashboard-saved`);
                        navigate(`${GLOBAL_PREFIX}/dashboard_my_deals_details`, {
                          state: { orderId },
                        });

                        syncSuccessOnClose();
                      }}
                    />
                  </Suspense>
                </VStack>
              </Stack>
            </ModalComponent>
            {/* ================================================PROGRESS======================================================== */}
            <ModalComponent isOpen={progressModalIsOpen} onClose={progressModalOnClose}>
              <Flex w="full" align={"center"} justify={"center"} mb="30px">
                <Lottie
                  style={{ width: "50px", height: "50px" }}
                  animationData={spinning_loader}
                  loop
                  autoplay
                />
              </Flex>

              <Box position="relative">
                <Stepper size="sm" index={activeStep} gap="0" colorScheme={"gray"}>
                  {steps.map((step, index) => (
                    <Step key={index}>
                      <StepIndicator bg="white">
                        <StepStatus complete={<StepIcon />} />
                      </StepIndicator>
                    </Step>
                  ))}
                </Stepper>
                <Progress
                  value={progressPercent}
                  position="absolute"
                  height="3px"
                  width="full"
                  top="10px"
                  zIndex={-1}
                  colorScheme={"gray"}
                  hasStripe
                  transition={"all .8s linear"}
                />
                <Flex w="full" align={"center"} justify={"center"} mt="30px">
                  <Text
                    alignSelf={"center"}
                    justifySelf={"center"}
                    fontSize={"18px"}
                    fontWeight={800}
                  >
                    {activeStepText}
                  </Text>
                </Flex>
              </Box>
            </ModalComponent>
          </Suspense>
        </Container>
      </Flex>
    </Box>
  );
};

